*{
  margin: 0;
}
.box{
  display:flex;
  background: rgb(70,70,70);
  top:0;
  left:0;
  width:100%;
  min-height: 100vh;
  height: 100%;
  position:fixed;
  justify-content:space-between;
  align-items: center;
  flex-direction: column;
  overflow:auto;
}
#imgMod{
  min-height: 70px;
  min-width: 70px;
  width:900px;
  max-width: 100%;
}
.box p{
  max-width: 900px;
  text-shadow:1px 1px black;
}
