*{
  margin: 0;
}
.boxKitchen{
  display:flex;
  background: rgb(70,70,70);
  top:0;
  left:0;
  width:100%;
  height:100vh;
  position:fixed;
  justify-content:space-between;
  align-items: center;
  flex-direction: column;
  overflow:auto;
  min-height: 100vh;
}
#imgag{
  min-height: 70px;
  width:900px;
  min-width: 100px;
}
.boxKitchen p{
  max-width: 900px;
  text-shadow:1px 1px black;
}
